.experience-wrap {
  font-size: 20px;
  font-size: 18px;
  background-color: #FFFFF3;
  flex: 100% 1;
  margin-bottom: calc(20px * 2);
  opacity: .87;
  -webkit-transition: all .4s ease-out;
  transition: all .4s ease-out; }
  .experience-wrap.active {
