.company-info {
  letter-spacing: 1px;
  color: #FFF;
  background-color: #566270;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.3);
  padding: 20px calc(20px * 2);
  position: relative;
  justify-content: center;
  display: flex;
  align-items: center; }
  .company-info:before {
    content: attr(data-idx);
    width: 50px;
    height: 100px;
    font-size: 26px;
    text-align: center;
    background-color: #566270;
    border: 1px solid #FFFFF3;
