@-webkit-keyframes ball {
  0% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px); }
  50% {
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px); }
  100% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px); } }

@keyframes ball {
  0% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px); }
  50% {
    -webkit-transform: translateY(-12px);
            transform: translateY(-12px); }
  100% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px); } }

@-webkit-keyframes ballShadow {
  0%, 100% {
    width: 88%; }
  50% {
    width: 90%;
    height: 12px;
    background-color: #eee;
    bottom: -20px; } }

@keyframes ballShadow {
  0%, 100% {
